import React, { Component } from "react";

// Pages
import Home from './Home'
import About from './About'
import Donate from './Donate'
import { render } from "@testing-library/react";

import {
    BrowserRouter,
    Routes,
    Route
} from 'react-router-dom'

function Page() {
    return (
        <BrowserRouter>
        <Routes>
            <Route path="/">
                <Home />
            </Route>
            <Route path="/About">
                <About />
            </Route>
            <Route path="/Donate">
                <Donate />
            </Route>
        </Routes>
        </BrowserRouter>
    )
}

export default Page;